import "./Sidebar.css";
import useStyles from "../../styleContext";
import axios from "axios";
import { useState } from "react";
function Prop(props) {
    return (
        <div className="prop">
            <p>{props.prop}</p>
            <input
                value={props.value}
                name={props.prop}
                onChange={props.onChange}
                type={props.type}
            />
        </div>
    );
}
function Sidebar() {
    const [, api, img, setImg] = useStyles();
    const [name, setName] = useState("");
    const [loading,setLoading] = useState(false)
    const handleFont = (e) => {
        api.start({
            fontSize: Number(e.target.value),
        });
    };
    const handleColor = (e) => {
        api.start({
            color: e.target.value,
        });
    };
    const handleUpload = () => {
        let style = document.querySelector(".text").getAttribute("style");
        setLoading(true)
        var reader = new FileReader();
        reader.onloadend = function () {
            const jsonData = {
                name,
                style,
                key: "bfda40-960964-bd6cdf-97152e-974c91",
                image: reader.result,
            };
            axios({
                method: "POST",
                url: "https://congrat.jeelalriyada.org/post-api.php",
                data: jsonData,
                headers: { "Content-Type": "application/json" },
            }).then((res) => {
                setLoading(false)
                if(res.data.message==='1'){
                    setImg(null)
                }
            });
        };
        reader.readAsDataURL(img);
    };
    return (
        <div className="sidebar">
            {img ? (
                <>
                    <Prop
                        prop="name"
                        onChange={(e) => setName(e.target.value)}
                        value={name}
                    />
                    <Prop
                        prop="width"
                        onChange={(e) => {
                            document
                                .querySelector("img")
                                .setAttribute("width", e.target.value);
                        }}
                    />
                    <Prop
                        prop="height"
                        onChange={(e) => {
                            document
                                .querySelector("img")
                                .setAttribute("height", e.target.value);
                        }}
                    />
                    <Prop prop="fontSize" onChange={(e) => handleFont(e)} />
                    <Prop
                        prop="color"
                        type="color"
                        onChange={(e) => handleColor(e)}
                    />
                    <button
                        className="center"
                        onClick={() => {
                            document.querySelector(".text").style.left = "50%";
                        }}
                    >
                        Center
                    </button>
                    <button disabled={loading} className="upload" onClick={handleUpload}>
                        {loading ?'Loading':'Upload'}
                    </button>
                </>
            ) : (
                <Prop
                    prop="Image"
                    type="file"
                    onChange={(e) => {
                        setImg(e.target.files[0]);
                    }}
                />
            )}
        </div>
    );
}

export default Sidebar;
